import React, { useState, Fragment } from "react";

import { Button, Icon } from "react-materialize";

import AdditionalInfo from "./AdditionalInfo";

const Table = ({ data, tableSettings }) => {
  // Unpack the tableSettings Object
  const {
    uid,
    columnsOnMainTable,
    columnProperties,
    columnsOnAdditionalInfo,
    colForTitleForAdditionalInfoPage,
    themeColor,
  } = tableSettings;

  // We need to assign an ID to the div that holds the additional info modal
  const modalDivId = "additional-info-modal";

  // useState to keep track of which datum
  // should appear on the additional Information modal
  const [
    selectedAdditionalInfoDatum,
    setSelectedAdditionalInfoDatum,
  ] = useState(null);

  // Helpers for rendering the main table
  // Render the head of the table
  const renderTableHead = (colNames, columnProperties) => {
    return (
      <thead>
        <tr>
          {colNames.map((colName) => (
            <th key={colName}>{columnProperties[colName].display}</th>
          ))}

          <th>Additional Info</th>
        </tr>
      </thead>
    );
  };

  // Render the body of the table
  const renderTableBody = (data, colNames) => {
    return data.map((datum, i) => renderTableRow(datum, colNames, i));
  };

  // Render a row of the table
  const renderTableRow = (datum, colNames, i) => {
    return (
      <tr
        key={datum[uid]}
        style={
          i % 2 === 0
            ? { backgroundColor: "#f5f5f5" }
            : { backgroundColor: "#fafafa" }
        }
      >
        {colNames.map((colName) => (
          <td key={datum[colName]}>
            <code>{datum[colName]}</code>
          </td>
        ))}

        <td>
          <Button
            className="modal-trigger"
            data-target={modalDivId}
            style={{ backgroundColor: themeColor }}
            onClick={() => {
              setSelectedAdditionalInfoDatum(datum);
            }}
          >
            <Icon>info</Icon>
          </Button>
        </td>
      </tr>
    );
  };

  // Main render function
  // Note that we include the additional info
  return (
    <Fragment>
      <table>
        {renderTableHead(columnsOnMainTable, columnProperties)}
        <tbody>{renderTableBody(data, columnsOnMainTable)}</tbody>
      </table>

      <AdditionalInfo
        columnsOnAdditionalInfo={columnsOnAdditionalInfo}
        colForTitleForAdditionalInfoPage={colForTitleForAdditionalInfoPage}
        columnProperties={columnProperties}
        selectedAdditionalInfoDatum={selectedAdditionalInfoDatum}
        setSelectedAdditionalInfoDatum={setSelectedAdditionalInfoDatum}
        themeColor={themeColor}
        modalDivId={modalDivId}
      ></AdditionalInfo>
    </Fragment>
  );
};

export default Table;
