import React, { useEffect } from "react";

import { Button } from "react-materialize";
import M from "materialize-css";

function AdditionalInfo({
  columnsOnAdditionalInfo,
  colForTitleForAdditionalInfoPage,
  columnProperties,
  selectedAdditionalInfoDatum,
  setSelectedAdditionalInfoDatum,
  themeColor,
  modalDivId,
}) {
  useEffect(() => {
    //initialize materialize

    const options = {
      onOpenStart: () => {
        // console.log("Open Start");
      },
      onOpenEnd: () => {
        // console.log("Open End");
      },
      onCloseStart: () => {
        // console.log("Close Start");
      },
      onCloseEnd: () => {
        setSelectedAdditionalInfoDatum(null);
      },
      inDuration: 250,
      outDuration: 250,
      opacity: 0.5,
      dismissible: false,
      startingTop: "4%",
      endingTop: "10%",
    };

    M.Modal.init(document.getElementById(modalDivId), options);
  });

  // Helpers for rendering

  // Render the title for the additional info modal
  const renderTitle = (title) => {
    return (
      <h4>
        Additional Details for <code>{title}</code>
      </h4>
    );
  };

  // Render the table containing the additional info
  const renderListItemCodeTable = (
    data,
    columnsOnAdditionalInfo,
    columnProperties
  ) => {
    return (
      <table>
        <tbody>
          {columnsOnAdditionalInfo.map((col, i) =>
            renderListItemCodeRow(data[col], columnProperties[col].display, i)
          )}
        </tbody>
      </table>
    );
  };

  // Render an individual row of the table
  const renderListItemCodeRow = (datum, display, i) => {
    return (
      <tr key={i}>
        <td>
          <b>{display}</b>
        </td>
        <td>
          <code>{datum}</code>
        </td>
      </tr>
    );
  };

  // Main render function
  if (selectedAdditionalInfoDatum) {
    return (
      <div id={modalDivId} className="modal">
        <div className="modal-content">
          {renderTitle(
            selectedAdditionalInfoDatum[colForTitleForAdditionalInfoPage]
          )}
          <div className="section">
            {renderListItemCodeTable(
              selectedAdditionalInfoDatum,
              columnsOnAdditionalInfo,
              columnProperties
            )}
          </div>
        </div>

        <div className="modal-footer">
          <Button
            className="modal-close"
            style={{ backgroundColor: themeColor }}
          >
            Close
          </Button>
        </div>
      </div>
    );
  } else {
    return null;
  }
}

export default AdditionalInfo;
